.App {
  text-align: center;
  /* padding: 20px; */
  min-height: 100vh;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  /* background-color: #282c34; */
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.dark_theme {
  background-color: #282c34;
  color: white;
}

.light_theme {
  background-color: #eee;
  color: #282c34;
}

.App-link {
  color: #61dafb;
}

.myInput {
  display: block;
  padding: 10px 0;
  font-size: 20px;
  margin-bottom: 10px;
  border-radius: 10px;
  outline: none;
  padding-left: 10px;
  width: calc(100% - 10px - 4px);
}

.myInput:focus {
  background-color: rgb(234, 250, 255);
}

.myInput_dark_theme {
  background-color: #505050;
  color: #fff;
  border-color: #303030;
}

.myInput_dark_theme:focus {
  background-color: #606060;
}

.page-container {
  max-width: 1000px;
  min-width: 200px;
  padding: 20px;
  margin: 0 auto;
}

.arrow-up {
  position: fixed;
  bottom: 20px; 
  right: 20px;
  font-size: 2em;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  background-color: #ddd;
  color: #303030;
}

.arrow-up-dark-theme {
  background-color: #404040;
  color: #fff;
}

.arrow-up:hover {
  background-color: #ccc;
  cursor: pointer;
}

.arrow-up-dark-theme:hover {
  background-color: #505050;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
