.message_container {
  //background-color: #daf9ff;
  background-color: #fff;
  padding: 20px;
  //margin: 20px;
  margin-bottom: 20px;
  text-align: left;
  border-radius: 20px;
  overflow: hidden;
  word-wrap: break-word;

  .message_flex {
    display: flex;
    justify-content: space-between;
  }

  .message_login {
    //color: rgb(46, 46, 204);
    color: green;
    font-size: 18px;
    margin-bottom: 20px;
  }

  .message_currency {
    color: green;
    font-size: 20px;
  }
  .message_date_prov {
    color: #a0a0a0;
    margin-bottom: 20px;
  }

  .message_content {
    font-size: 20px;
    color: #202020;

    a {
      color: blue;
    }
  }
}

.message_container:nth-last-child(1) {
  margin-bottom: 0;
}

.message_container_dark_theme {
  background-color: #505050;

  .message_login, .message_currency {
    color: lightgreen;
  }

  .message_date_prov {
    color: #a0a0a0;
  }

  .message_content {
    color: #fff;

    a {
      color: lightblue;
    }
  }
}